.image-background-text {
    width: 100%;
    .container {
        max-width: 1392px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        padding: 0;

        .image-content {
            position: abosolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 650px;
            overflow: hidden;
            transition: 0.3s;

            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 100%;
                background: rgba(0, 0, 0, 0.3);
                backdrop-filter: blur(2px);
            }
            
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            @include tablet {
                height: 450px;
            }
        }

        .copy-content {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            transition: 0.3s;
            padding: 0 24px;
            .copy-heading,
            .copy-body {
                color: white;
                text-align: center;
            }

            .recruit-btn {
                margin-top: 72px;
                a {
                    display: block;
                    color: white;
                    text-decoration: underline;
                    transition: 0.3s ease;
                    &:hover {
                        opacity: 0.5;
                    }
                }
                @include tablet {
                }

                @include mobile {
                }
            }

            @include tablet {
            }

            @include mobile {
            }
        }
    }
}